<template>
  <div class="addressmanage padd">
    <ul>
        <li class="clearfix">
            <span class="two_t_m fl w78 name">联系人</span>
            <p class="fl">
                <input type="text" placeholder="姓名" v-model="addInfo.name" class="two_t_h">
            </p>
        </li>
        <li class="clearfix">
            <span class="radio fl" :class="[addInfo.sex==1?'check':'uncheck']" @click="addInfo.sex=1">先生</span>
            <span class="radio fl" :class="[addInfo.sex==0?'check':'uncheck']" @click="addInfo.sex=0">女士</span>
        </li>
        <li class="clearfix">
            <span class="two_t_m fl w78">电话</span>
            <input type="nunber" v-model="addInfo.tel"  placeholder="电话号码" class="fl two_t_h">
        </li>
        <li class="clearfix">
            <span class="two_t_m fl w78">地址</span>
            <input type="text" v-model="addInfo.address"  placeholder="请选择" readonly class="fl two_t_h">
            <span class="fr two_t_m">></span>
        </li>
        <li class="clearfix">
            <span class="two_t_m fl w78">详细地址</span>
            <textarea class="fl two_t_h" v-model="addInfo.address"  placeholder="详细地址，如具体门牌号"></textarea>
        </li>
        <li class="clearfix">
            <span class="two_t_m fl w78">标记为</span>
            <span class="radio fl" :class="[addInfo.type==1?'check':'uncheck']" @click="addInfo.type=1">家</span>
            <span class="radio fl" :class="[addInfo.type==0?'check':'uncheck']" @click="addInfo.type=0">公司</span>
            <span class="radio fl" :class="[addInfo.type==2?'check':'uncheck']" @click="addInfo.type=2">学校</span>
        </li>
    </ul>
    <button class="bluebtn">确定</button>
    <button class="delete" @click="showConfirm=true" v-if="!$route.query.isAdd">删除地址</button>
    <!-- {{this.$route.query.address}} -->
    <confirm v-model="showConfirm" @on-confirm="showConfirm=false" title="确定删除？"></confirm> 
  </div>
</template>

<script>
import { Confirm } from 'vux'

export default {
  name: "Addressmanage",
  data() {
    return {
      sex: 1,
      type: 0,
      showConfirm: false,
      addInfo: {
        name: '',
        type: '',
        tel: '',
        address: '',
        sex: 1
      }
    };
  },
  created(){
    if(!this.$route.query.isAdd) {
      this.addInfo = this.$route.query.address
    }
    console.log(this.$route.query.address)
  },
  methods: {
    
  },
  components: {
    Confirm
  }
};
</script>

<style lang="less">
.addressmanage {
  font-size: 0.16rem;
  ul {
    li {
      padding: 0.15rem 0;
      border-bottom: 0.01rem solid #f2f2f2;
      &:nth-child(1) {
        padding: 0;
        border: none;
      }
      &:nth-child(2) {
        padding: 0.1rem 0rem 0.15rem 0.78rem;
      }
      &:nth-last-child(1) {
        border: none;
        margin-bottom: 0.15rem;
      }
      .w78 {
        width: 0.78rem;
      }
      .name {
        padding: 0.15rem 0rem;
      }
      p {
        padding: 0.15rem 0rem;
        border-bottom: 0.01rem solid #f2f2f2;
      }
      input {
        width: 2.1rem;
        border: none;
      }
      textarea {
        width: 2.42rem;
        min-height: 0.44rem;
        resize: none;
        border: none;
      }
      .radio {
        border-radius: 0.03rem;
        font-family: PingFangSC-Regular;
        font-size: 0.12rem;
        letter-spacing: 0;
        margin-right: 0.2rem;
        width: 0.4rem;
        height: 0.2rem;
        text-align: center;
      }
      .uncheck {
        border: 0.01rem solid rgba(0, 0, 0, 0.2);
        color: #7f7f7f;
      }
      .check {
        border: 0.01rem solid rgba(0, 0, 0, 0.9);
        color: #191919;
      }
    }
  }
  .delete {
    width: 100%;
    height: 0.4rem;
    border: 0.01rem solid #e7290f;
    border-radius: 0.06rem;
    font-family: PingFangSC-Regular;
    font-size: 0.16rem;
    color: #e7290f;
    letter-spacing: 0;
    background-color: #fff;
    margin-top: 0.2rem;
  }
}
</style>




